@import './reset.scss';


$메인칼라 : #ff0000;

.my-alert {
    background : #eeeeee;
    padding: 15px;
    border-radius: 5px;
    max-width: 500px;
    width: auto;
    margin: auto;
}

.my-alert2 {
    @extend .my-alert;
    background : #ffe591;
}

@mixin 함수(){
    background : blue;
    padding: 15px;
    border-radius: 5px;
    max-width: 500px;
    width: auto;
    margin: auto;
}

.my-alert3 {
    @include 함수()
}

.my-alert p {
    margin-bottom: 0;
}

.red{
    color : $메인칼라;
}

div.container h4{
    color : blue;
}

div.container p{
    color : green;
}

div.container{
    h4{
        color : blue;    
    }
    p{
        color : green;
    }
}